.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

.sidebarRow:hover{
    background: #4d4b50;
    border-radius: 10px;
}

.sidebarRow > h4{
    margin-left: 20px;
    font-weight: 600;
    color: #fff;
}

.sidebarRow > .MuiSvgIcon-root{
    font-size: xx-large;
    color: #2e81f4;
}