.login{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.login > div > img{
    object-fit: contain;
    height: 150px;
}

.login > div{
    display: flex;
    flex-direction: column;
}

.login > button{
    width: 300px;
    background: #2e81f4;
    color: #eff2f5;
    font-weight: 800;
}

.login > button:hover{
    background: #fff;
    color: #2e81f4;
}