.post{
    width: 100%;
    margin-top: 30px;
    border-radius: 15px;
    background: #fff;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    overflow: hidden;
    background: rgba(52, 52, 52 , 0.6);
}

.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.post__avatar{
    margin-top: 10px;
}

.post__topInfo > h3{
    font-size: large;
    margin-left: 10px;
    color: #fff;
}

.post__topInfo > p{
    font-size: small;
    color: gray;
    margin-left: 10px;
}

.post__bottom{
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}

.post__bottom > p{
    color: #fff;
    font-size: x-large;
}

.post__image > img{
    width: 100%;
}


.post__options{
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}

.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
}

.post__option > p{
    margin-left: 10px;
}

.post__option:hover{
    background: #eff2f5;
    border-radius: 10px;
}