.header{
    display: flex;
    padding: 15px 20px;
    justify-content: space-between;
    position: sticky;
    background: #fff;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
    background: rgba(28,30,33,1);
    border-bottom: .1px solid lightgray;
}

.header__left{
    display: flex;
    justify-content: space-evenly;
}

.header__input{
    display: flex;
    align-items: center;
    background: rgba(77, 75, 80 , 0.9);
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}

.header__input > input{
    border: none;
    background: transparent;
    outline-width: 0;
}

.header__input > input::placeholder{
    color: #fff;
}

.header__left > img{
    height: 40px;
}

.header__middle{
    display: flex;
    flex: 1;
    justify-content: center;
}

.header__option > .MuiSvgIcon-root{
    color: gray;
}

.header__option:hover > .MuiSvgIcon-root{
    color: #2e81f4;
}

.header__option--active > .MuiSvgIcon-root{
    color: #2e81f4;
}

.header__option--active{
    border-bottom: 4px solid #2e81f4;
}

.header__option{
    display: flex;
    align-items: center;
    padding:0 30px ;
    cursor: pointer;
}

.header__option:hover{
    background: rgba(77, 75, 80 , 0.9);
    border-radius: 10px;
    padding:0 30px ;
    border-bottom: none;
}

.header__info{
    display: flex;
    align-items: center;
    color: #fff;
}

.header__info > h4{
    margin-left: 10px;
}

.header__right{
    display: flex;  
}