.story{
    position: relative;   
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgb(238, 238, 255 , 0.5);
    border-radius: 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: transform 100ms ease-in;
}

.story:hover{
    transform: scale(1.07);
}

.story__avatar{
    margin: 10px;
    border: 4px solid #2e81f4;
}

.story > h4{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: #fff;
}